<template>
  <div
    class="d-flex justify-center align-center h-full"
    style="background:#ececec"
  >
    <v-row>
      <v-col cols="12" sm="8" md="8" offset-md="2" offset-sm="2">
        <v-card class="d-flex w-full">
          <v-row>
            <v-col cols="12" md="5">
              <v-form
                @submit.prevent="login"
                ref="login"
                lazy
              >
                <div
                  class="d-flex justify-center py-8"
                >
                  <img
                    class="w-1/2"
                    src="/logo.png"
                    alt=""
                  />
                </div>
                <div class="px-16">
                  <v-text-field
                    v-model="form.username"
                    :rules="[ v => !!v || 'Email or Username is required' ]"
                    label="Email or Username"
                    required
                    outlined
                  ></v-text-field>
                  <v-text-field
                    class="py-2"
                    v-model="form.password"
                    :rules="[ v => !!v || 'Password is required' ]"
                    label="Password"
                    type="password"
                    required
                    outlined
                  ></v-text-field>
                  <v-btn
                    type="submit" 
                    color="success"
                    class="mr-4"
                    :class="{ 'pointer-events-none': submitting }"
                    :loading="submitting"
                  >
                    Login 
                    <v-icon class="ml-2" small>
                      fas fa-sign-in-alt
                    </v-icon>
                  </v-btn>
                </div>
              </v-form>
              <div
                class="d-flex w-full justify-end px-16"
                style="font-size:12px;"
              >
                <a href="">Not yet registered ? </a>
              </div>
            </v-col>
            <v-col
              cols="12" md="7"
              class="d-sm-none d-md-flex"
            >
              <div class="grey darken-4 w-full h-full"></div>
            </v-col>
          </v-row>
        </v-card>
      </v-col>
    </v-row>
  </div>
</template>
<script>
export default {
  data: () => ({
    form: {},
    submitting: false
  }),
  methods: {
    async login () {
      if (this.$refs.login.validate()) {
        try {
          this.submitting = true
          // Authenticate
          this.form.grant_type = 'password'
          this.form.client_id = process.env.VUE_APP_CLIENT_ID
          this.form.client_secret = process.env.VUE_APP_CLIENT_SECRET
          const auth = await this.$axios.post('/oauth/token', this.form)
          // Get Profile
          const token = auth.data.token_type + ' ' + auth.data.access_token
          this.$axios.defaults.headers.common['Authorization'] = token
          const profile = await this.$store.dispatch('getProfile')
          // Save Info to Cookies
          this.$cookies.set('token', token, auth.data.expires_in)
          this.$cookies.set('role', profile.role.name, auth.data.expires_in)
          // Redirect
          this.gate(profile.role.name)
        } catch (e) {
          console.log(e)
          this.showSnackbar('Invalid Credentials')
        } finally {
          this.submitting = false
        }
      }
    },
    gate (role) {
      switch (role) {
        case 'Admin':
          this.$router.push('/admin/dashboard')
          break
        case 'Seller':
          this.$router.push('/seller/dashboard')
          break
        default:
          this.$router.push('/user/dashboard')
          break
      }
    }
  }
}
</script>
